.bnwc-modal-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 20200202;
  background: rgba(0, 0, 0, 0.8);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: all 0.4s ease-in-out;
}
.bnwc-modal-mask.hide {
  display: none;
}
.bnwc-modal-mask .bnwc-modal {
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  width: 90%;
}
.bnwc-modal-mask .bnwc-modal .bnwc-modal-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  font-size: 16px;
  height: 24px;
  padding: 0 10px;
}
.bnwc-modal-mask .bnwc-modal .bnwc-modal-header .bnwc-modal-title {
  color: #666;
}
.bnwc-modal-mask .bnwc-modal .bnwc-modal-header .bnwc-modal-times {
  color: #999;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  transition: color 0.4s ease-in-out;
}
.bnwc-modal-mask .bnwc-modal .bnwc-modal-header .bnwc-modal-times:hover {
  color: #333;
}
.bnwc-modal-mask .bnwc-modal .bnwc-modal-body {
  padding: 20px;
}
.bnwc-modal-mask .bnwc-modal .bnwc-modal-footer {
  padding: 10px;
}
